html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 16px; /*1rem = 10px */
  font-family: "Prompt", sans-serif;
  background-color: #1f2126;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 87.02px;
}

.contenedor {
  max-width: 120rem;
  width: 80%;
  margin: 0 auto;
}

p {
  font-size: 1.6rem;
  color: #FFFFFF;
  line-height: 1.4;
}

h1, h2, h3 {
  margin: 0 0 1rem 0;
  color: #FFFFFF;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  display: block;
}

li {
  color: #FFFFFF;
}

.header {
  background-color: rgba(0, 0, 0, 0.875);
  backdrop-filter: blur(5px);
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    z-index: 1;
  }
}
.header .contenido-header {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .logo-header {
  width: 10rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header .logo-header {
    margin-bottom: 0;
  }
}

@media (min-width: 768px) {
  .nav-principal {
    display: flex;
    gap: 2rem;
  }
}
.nav-principal a {
  display: block;
  margin-bottom: 1rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 100;
  text-decoration: none;
  transition: 0.5s;
}
.nav-principal a:hover {
  color: #FF4081;
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}

.video {
  position: relative;
  max-height: 60rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: rgb(255, 64, 129);
  background: linear-gradient(330deg, rgba(255, 64, 129, 0.6965379902) 0%, rgba(97, 218, 251, 0.7021402311) 100%);
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  font-size: 6rem;
  margin: 0;
}
.contenido-video p {
  background-color: #FF4081;
  display: inline-block;
  padding: 1rem 3rem 1rem 3rem;
  margin: 0;
  font-size: 2rem;
  border-radius: 0 4rem 4rem 4rem;
  transition: 1s;
}
.contenido-video p:hover {
  background-color: #61DAFB;
  cursor: pointer;
}

.edm .nombre-escenario:after {
  background-image: url(/build/img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(/build/img/icono_rock.png);
}

.about-festival {
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.about-festival .img-festival {
  position: relative;
  overflow: hidden;
  border-radius: 1rem;
}
.about-festival img {
  width: 100%; /* Hace que la imagen ocupe el 100% del ancho del div */
  height: 100%;
  object-fit: cover;
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  font-size: 4rem;
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #61DAFB;
  font-weight: 400;
  margin-bottom: 1;
}
.contenido-festival .info {
  background-color: #33363d;
  display: inline-block;
  padding: 2.5rem;
  border-radius: 0 3rem 3rem 3rem;
  font-weight: 200;
  margin: 0;
}

.lineup {
  background-color: #24262c;
  opacity: 0.8;
  background-image: linear-gradient(135deg, #1f2126 25%, transparent 25%), linear-gradient(225deg, #1f2126 25%, transparent 25%), linear-gradient(45deg, #1f2126 25%, transparent 25%), linear-gradient(315deg, #1f2126 25%, #24262c 25%);
  background-position: 29px 0, 29px 0, 0 0, 0 0;
  background-size: 58px 58px;
  background-repeat: repeat;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
}
.lineup h3 {
  font-weight: 800;
  font-size: 6rem;
  color: #61DAFB;
  text-align: center;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 3.5rem 0;
  font-size: 2rem;
  font-weight: 500;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.escenarios-contenedor p {
  color: #0e1013;
  font-size: 4rem;
  font-weight: 900;
}
.escenarios-contenedor p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}
.escenarios-contenedor .escenario {
  padding: 1rem 0 1rem 0;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .escenarios-contenedor .escenario {
    margin-bottom: 0;
  }
}
.escenarios-contenedor .iz {
  border-radius: 5rem 0 0 5rem;
}
.escenarios-contenedor .der {
  border-radius: 0 5rem 5rem 0;
}

.bg-primary {
  background-color: #61DAFB;
}
.bg-primary li, .bg-primary p {
  color: #007997;
}
.bg-primary li:nth-child(odd) {
  background-color: #2fcefa;
}

.bg-accent {
  background-color: #FF4081;
}
.bg-accent li, .bg-accent p {
  color: #ffe4ff;
}
.bg-accent li:nth-child(odd) {
  background-color: #ff216d;
}

.calendario {
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2rem;
  text-align: center;
}
.calendario span {
  font-weight: 700;
  font-size: 2.3rem;
}

.galeria {
  padding: 0 0 8rem 0;
  background-color: #FF4081;
  width: auto;
}
.galeria h3 {
  font-weight: 800;
  font-size: 6rem;
  color: #ffe4ff;
  text-align: center;
  padding: 3rem 0;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.43);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #e0e0e0;
  color: #1f2126;
  display: grid;
  place-items: center;
  font-weight: 500;
  font-size: 2.3rem;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  font-weight: 800;
  font-size: 6rem;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .nombre-pase {
  text-align: center;
  font-size: 3rem;
  font-weight: 500;
}
.pase-header p, .pase-footer p {
  text-align: center;
  margin: 0;
  padding: 1rem;
}
.pase-body ul {
  padding: 0;
  list-style: none;
  margin: 0;
}
.pase-body ul li {
  background-color: #FFFFFF;
  padding: 1.5rem;
  border-bottom: solid 1px #e1e1e1;
}
.pase .pase-header {
  background-color: #61DAFB;
  border-radius: 1rem 1rem 0 0;
}
.pase .pase-header p {
  color: #1f2126;
  text-transform: uppercase;
  font-size: 1.5rem;
}
.pase .pase-body li {
  color: #1f2126;
}
.pase .pase-footer {
  background-color: #FF4081;
  border-radius: 0 0 1rem 1rem;
}
.pase .pase-footer p {
  font-size: 2.5rem;
  font-weight: 600;
}

.site-footer {
  background-color: #FF4081;
  padding: 2rem;
}
.site-footer p {
  margin: 0;
  color: #ffe4ff;
  text-align: center;
}