@use '../base/variables' as v;


@mixin mobile {
    @media (min-width: v.$mobile){
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop){
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: v.$desktopXL){
        @content;
    }
}

@mixin grid ($columnas, $espaciado){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin resetear-lista{
    padding: 0;
    list-style: none;
}

@mixin titulo-grande ($color) {
    font-weight: 800;
    font-size: 6rem;
    color: $color;
    text-align: center;
}
